@import url('https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@400;800&display=swap');

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.maindiv {
  width: 100%;
  height: 100vh;
  background: linear-gradient(to bottom, hsl(248, 17%, 9%), hsl(255, 22%, 4%));
  ;
  font-family: 'JetBrains Mono', monospace;
  display: flex;
  justify-content: center;
  align-items: center;
}

.centraldiv {
  width: 600px;
  height: 750px;
}

.titlediv {
  width: 100%;
  height: 8%;
  display: flex;
  justify-content: center;

}

h1 {
  margin: 0;
  color: #807C92;
  font-size: 27px;
}

.bodydiv {
  width: 100%;
  height: 92%;
}

.searchbodydiv {
  height: 90px;
  background-color: hsl(247, 11%, 15%);
  display: flex;
  align-items: center;
  justify-content: center;
}

.passwordh3 {
  font-size: 30px;
}

.containersenha {
  height: 90%;
  width: 90%;
  display: flex;
  align-items: center;
  color: white;
}

.rightsvg {
  filter: invert(56%) sepia(24%) saturate(264%) hue-rotate(211deg) brightness(84%) contrast(84%);
  width: 13px;
  margin-left: 12px;
}

.copyDiv {
  width: 50%;
  height: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;

}

.containersenha2 {
  height: 100%;
  width: 50%;
  /* background-color: black; */
  display: flex;
  align-items: center;
}

.copiarsvg {
  filter: invert(56%) sepia(24%) saturate(264%) hue-rotate(211deg) brightness(84%) contrast(84%);
  width: 30px;
}

.configbodydiv {
  background-color: hsl(247, 11%, 15%);
  height: 570px;
  margin-top: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.generalcontainer {
  width: 90%;
  height: 90%;
  /* background-color: red; */
}

.topcld {
  width: 100%;
  height: 90px;
  /* background-color: gold; */
  display: flex;
  flex-direction: column;
}

.topf {
  display: flex;
  justify-content: space-between;
  height: 50%;
}

span {
  color: hsl(252, 11%, 91%);
  font-weight: 800;
}

.topfspanT {
  font-size: 20px;
  margin-top: 5px;
}

.topfspanN {
  font-size: 35px;
  color: hsl(127, 100%, 82%);
}

.bottomf {
  height: 50%;
  display: flex;
  align-items: flex-end;
}

.range {
  appearance: none;
  width: 100%;
  height: 100%;
  background: hsl(255, 22%, 4%);
  outline: none;
  opacity: 0.7;
  -webkit-transition: .2s;
  transition: opacity .2s;
  position: absolute;
  z-index: 999;
}

.range::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 30px;
  height: 30px;
  background: hsl(252, 11%, 91%);
  cursor: pointer;
  margin-top: -5px;
  border-radius: 25px;
}

.range::-webkit-slider-thumb:hover {
  background-color: hsl(255, 22%, 4%);
  border: 4px solid hsl(127, 100%, 82%);
  width: 35px;
  height: 35px;
}

.carregamento {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 1;
}

.carregamentodiv {
  position: relative;
  width: 100%;
  background-color: transparent;
  height: 10px;
  display: flex;
  flex-direction: row;

}

.loadingdiv {
  width: 0%;
  background-color: hsl(127, 100%, 82%);
  z-index: 99;
}

.spanChoice {
  margin-left: 20px;
  font-size: 20px;
}

.quadradocheck {
  cursor: pointer;
  width: 15px;
  height: 15px;
  border: 3px solid white;
}

.containercheck {
  width: 100%;
  height: auto;
  margin-top: 30px;
}

.containercheckC {
  height: 50px;
  display: flex;
  align-items: center;
}

.checkboxi {
  width: 30px;
  height: 30px;
  background-color: transparent;
  -webkit-appearance: none;
  border: 2px solid white;
}

.checado {
  background-color: hsl(127, 100%, 82%);
  background-image: url('./images/correto.svg');
  background-size: 15px;
  background-position: center;
  border: none;
  width: 21px;
  height: 21px;
  background-repeat: no-repeat;
}

.divForca {
  width: 100%;
  height: 80px;
  background-color: hsl(248, 15%, 11%);
  margin-top: 20px;
  display: flex;
}

.first-flex {
  width: 50%;
  height: 100%;
}

.second-flex {
  width: 50%;
  height: 100%;
}

.first-flex {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  /* background-color: red; */
}

.second-flex {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.strenghSpan {
  font-size: 20px;
  margin-left: 30px;
  color: hsl(251, 9%, 53%);
}

.divqdds {
  display: flex;
  align-items: center;
}

.qd {
  width: 5px;
  height: 25px;
  border: 3px solid white;
  margin-right: 10px;
}

.qd4 {
  margin-right: 30px;
}

.copied-span {
  color: hsl(127, 100%, 82%);
  font-size: 20px;
  margin-right: 20px;
  display: none;
}

.submitbtndiv {
  width: 100%;
  background-color: hsl(248, 15%, 11%);
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  cursor: pointer;
}

.block {
  display: inline !important;
}

.ativado {
  background-color: hsl(127, 100%, 82%);
}

.greencolorsvg {
  filter: invert(89%) sepia(10%) saturate(1349%) hue-rotate(69deg) brightness(101%) contrast(104%);
  cursor: pointer;
}

.generateh2 {
  color: hsl(251, 9%, 53%);
  font-size: 20PX;
  position: relative;
}

.strenghspan {
  font-size: 25px;
  font-weight: 600;
  margin-right: 20px;
}

.tooweak {
  background-color: hsl(0, 91%, 63%);
  width: 11px;
  height: 31px !important;
  border: none;
}

.weak {
  border: none;
  background-color: hsl(13, 95%, 66%);
  width: 11px;
  height: 31px !important;
}

.cursornormal {
  cursor: default;
}

.medium {
  border: none;
  background-color: hsl(42, 91%, 68%);
  width: 11px;
  height: 31px !important;
}

.strong {
  width: 11px;
  height: 28px;
  height: 31px !important;
  border: none;
  background-color: hsl(127, 100%, 82%);
}

@media screen and (min-width:1000px) and (max-height:750px) {
  .centraldiv {
    max-height: 600px;
  }

  .searchbodydiv {
    height: 70PX;
  }

  .configbodydiv {
    margin-top: 20px;
    max-height: 450px;
  }

  .topcld {
    height: 60px;
  }

  .containercheck {
    margin-top: 20px;
  }

  .containercheckC {
    height: 40px;
  }

  .divForca {
    height: 60px;
  }

  .submitbtndiv {
    margin-top: 20px;
    height: 60px;
  }
}

@media screen and (max-width:610px) {
  .centraldiv {
    width: 95%;
  }

  .strenghspan {
    font-size: 20px;
  }
}

@media screen and (max-width:500px) {
  .titlediv h1 {
    font-size: 18px;
  }

  .titlediv {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
  }
  .strenghspan{
    font-size: 15px;
    margin-right: 10px;
  }
  .strenghSpan{
    margin-left: 10px;
  }
  .first-flex{
    width: 40%;
  }
  .second-flex{
    width: 60%;
  }
  .copiarsvg {
    width: 25px;
  }

  .searchbodydiv {
    height: 60px;
  }

  .bottomf {
    height: 25px;
  }

  .containercheck {
    margin-top: 10px;
  }

  .spanChoice {
    font-size: 17px;
  }

  .divForca {
    height: 60px;
  }

  .submitbtndiv {
    margin-top: 20px;
  }

  .configbodydiv {
    max-height: 470px;
    margin-top: 15px;
  }

  .centraldiv{
    height: auto;
  }

  .containercheckC {
    height: 40px;
  }
}
@media screen and (max-width:389px) {
  .strenghSpan{
    font-size: 18px;
  }
  .first-flex{
    width: 30%;
  }
  .second-flex{
    width: 70%;
  }
}
@media screen and (max-width:359px) {
  .spanChoice{
    font-size: 13px;
  }
  .quadradocheck{
    height: 10px;
    width: 10px;
  }
  .maindiv{
    height: auto;
  }
  .submitbtndiv{
    margin-top: 10px;
    height: 60px;
  }
  .bodydiv{
    height: 95%;
  }
  .titlediv{
    height: 5%;
  }
  .configbodydiv{
    max-height: 450px;
    height: auto;
  }
}